}

.cast-crew-link {
  text-decoration: none;
  color: darkblue;
}

.loading {
  height: 100vh;
  text-align: center;
  margin-top: 20%;
}

#movie-list-rating {
  color: grey;
  margin: 0px;
  padding: .6rem 1.25rem 0rem 1.25rem;
  font-weight: 400;
  font-size: 1rem
}

#movie-list-title {
  padding: 0.3rem 1.25rem .75rem 1.25rem;
  height: auto;
  font-size: 1.10rem;
  font-weight: 475;
}

.rating {
  color: grey;
}

.overview {
  text-align: justify;
}

.review-box {
  resize: none;
}

#review-heading {
  margin-bottom: 20px;
}

.basic-details {
  color: grey;
}

#similar-movies-heading {
  margin-left: 50px;
}

body {
  font-family: Helvetica, sans-serif;
}

.react-autosuggest__container {
  position: relative;
  width: 100%;
}

.react-autosuggest__input {
  width: 100%;
  height: 100%;
  padding: 10px 20px;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
  overflow: scroll;
  height: 250px;
  margin-top: -10px;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 51px;
  width: 100%;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}

.search-suggestions-image {
  width: 12%;
  height: 12%;
}

.search-suggestions-movie-title {
  padding: 10px 20px;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
}

.search-suggestions-rating {
  margin-left: 10px;
}

#search-suggestions-card {
  text-decoration: none;
  border: 0px;
  background-color: transparent;
  display: inline-block;
}

